/**
* 系统-数据字典组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center
                   width="650px">
            <div class="wrop" v-loading="loading">
                <div class="left">
                    <el-button @click="downExcel" icon="el-icon-download" type="success">下载模板</el-button>
                    <p>（ 提示：若无模板，请先下载模板，再填写上传。)</p>
                </div>
                <div class="right">
                    <el-upload
                            :action="uploadUrl"
                            :auto-upload="false"
                            :before-upload="beforeUpload"
                            :file-list="fileList"
                            :on-preview="handlePreview"
                            :on-remove="handleRemove"
                            :on-success="onSuccess"
                            class="upload-demo"
                            ref="upload">
                        <el-button size="small" slot="trigger" type="primary">选取文件</el-button>
                        <el-button @click="submitUpload" class="btn-css" icon="el-icon-upload2" size="small"
                                   type="success">上传
                        </el-button>
                        <div class="el-upload__tip" slot="tip">请上传Excel文件</div>
                    </el-upload>
                </div>

            </div>

        </el-dialog>
    </div>
</template>
<script>
    import {caseBillApi, downloadBillApi} from "../../../../api/case";

    let that;
    export default {
        name: 'see',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: '编辑'
            },

        },
        data() {
            return {
                uploadUrl: caseBillApi,
                loading: false,
                fileList: [],

            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
            },
            handlePreview(file) {
            },
            beforeUpload(file) {
                this.loading = true;
            },
            //上传成功回调
            onSuccess(data) {
                this.$message({type: 'success', message: '上传成功!'});
                this.loading = false;
                this.$emit('getList');
                this.closeDialog();
            },

            submitForm() {
                this.closeDialog();
            },
            downExcel() {
                window.location.href = downloadBillApi
            }


        }
    }
</script>
<style lang='less' scoped>
    .wrop {
        min-height: 100px;
        display: flex;
        justify-content: space-between;

        .left {
            width: 200px;
            margin-right: 10px;

            p {
                font-size:  @fontSize ;
                color: #ccc;
                padding-top: 5px
            }
        }

        .right {
            width: 550px;
            padding-left: 20px

        }
    }

    .btn-css {
        margin-left: 10px;
    }
</style>


